import React from 'react';
import PropTypes from 'prop-types';
import StaticInfoPanel from './StaticInfoPanel';

import {ReactComponent as CallPhoneIcon} from "../svg/call-phone.svg";
import {ReactComponent as CalendarDatesIcon} from "../svg/calendar-dates.svg";
import {ReactComponent as CreditCardIcon} from "../svg/creditcard.svg";
import {ReactComponent as AlertOctagon} from "../svg/alert-octagon.svg";


class NamePanel extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      disabled: this.props.isDisabled,
    };
  }

  render() {
    const {mobile, birthday, cardid, name, surname} = this.props;

    return (
      <div className="block nameBlock">

        <div className="blockContent nameBlockContent">

          <div className="nameOverflowContainter">
            <div className="nameContainer">
              <p className="name">{name}</p>
              <p className="surname">{surname}</p>
            </div>
          </div>

          <div className="staticInfoContainer">
            <div className="InfoContainer">
              {this.state.disabled ? <StaticInfoPanel className="staticInfoPanel error" text="Карта заблокирована" icon={<AlertOctagon style={{fill: "red"}}/>} error={true}/> : <div style={{height: "43px"}}></div>}
              <StaticInfoPanel className="staticInfoPanel" text={mobile !== "" ? mobile : "Не указан"} icon={<CallPhoneIcon />} />
              <StaticInfoPanel className="staticInfoPanel" text={birthday} icon={<CalendarDatesIcon />} />
              <StaticInfoPanel className="staticInfoPanel" text={cardid} icon={<CreditCardIcon />} />
            </div>
          </div>

          <div className="clear"></div>

        </div>

      </div>
    )
  }
}

NamePanel.propTypes = {
  name: PropTypes.string.isRequired,
  surname: PropTypes.string.isRequired,
  mobile: PropTypes.string.isRequired,
  birthday: PropTypes.string.isRequired,
  cardid: PropTypes.string.isRequired,
  isDisabled: PropTypes.bool.isRequired
}

export default NamePanel;