import React from 'react';
import PropTypes from 'prop-types';
import TextWithLeftIcon from './TextWithLeftIcon';
import { minutesSuffix } from './utils';

import {ReactComponent as TimerIcon} from "../svg/timer.svg";

class Minute extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      
    };
  }

  formatMinutes() {
    return this.props.minutes + " минут" + minutesSuffix(this.props.minutes);
  }

  formatAccessibleFor(accessibleFor) {
    if (accessibleFor.length === 4) {
      return "Доступны для всех соляриев";
    }
    if (accessibleFor.length === 0) {
      return "Недоступны для всех соляриев";
    }
    let s = "Доступны для ";
    for (let i = 0; i < accessibleFor.length - 2; i++) {
      s += accessibleFor[i] + ", ";
    }
    if (accessibleFor.length >= 2) {
      s += accessibleFor[accessibleFor.length - 2] + " и " + accessibleFor[accessibleFor.length - 1];
    } else {
      s += accessibleFor[accessibleFor.length - 1];
    }
    s += " солярия";
    return s;
  }

  formatExpireDate() {
    return "Действует до " + this.props.expireDate;
  }

  render() {
    return (
      <div className="minute">
        <p className="semiTitle">{this.formatMinutes()}</p>
        <div className="minuteDescriptionWrapper">
          <div className="textWrapper">
            <p className="light">{this.formatAccessibleFor(this.props.accessibleFor)}</p>
          </div>
        </div>
        <div className="minuteDescriptionWrapper">
          <TextWithLeftIcon icon={<TimerIcon />} text={this.formatExpireDate()}/>  
        </div>
      </div>
    )
  }
}

Minute.propTypes = {
  minutes: PropTypes.number.isRequired,
  accessibleFor: PropTypes.arrayOf(PropTypes.string),
  expireDate: PropTypes.string.isRequired
}

export default Minute;