import React from 'react';
import PropTypes from 'prop-types';
import TextWithLeftIcon from './TextWithLeftIcon';

import {ReactComponent as TimerIcon} from "../svg/timer.svg";

class Present extends React.Component {
  constructor(props) {
    super(props);

    this.state = {

    };
  }

  formatExpireDate() {
    return "Действует до " + this.props.expireDate;
  }

  render() {
    return (
      <div className="present">
        <div className="textWrapper">
          <p className="semiTitle">{this.props.name}</p>
        </div>
        <div className="minuteDescriptionWrapper">
          <TextWithLeftIcon icon={<TimerIcon />} text={this.formatExpireDate()}/>  
        </div>
      </div>
    )
  }
}

Present.propTypes = {
  name: PropTypes.string.isRequired,
  expireDate: PropTypes.string.isRequired,
}

export default Present;